import axios from 'axios';
import { loginreq, req, axiosGet } from './axiosFun';

//  
export const login = (params) => { return loginreq("post", "/api/login", params) };
// 
export const loginout = () => { return axios.get("/api/logout?usertoken=" + sessionStorage.getItem('logintoken')).then(res => res.data) };

/**
 *  
 **/
//-token
export const isTokenCanUse = () => {return axios.get("/api/isTokenTimeOut?usertoken="+sessionStorage.getItem('logintoken')).then(res => res.data)};
// -
export const userList = (params) => { return axiosGet("/api/getUserList", params) };
// -
export const userSave = (params) => { return req("post","/api/postUser", params) };
// -
export const userDelete = (params) => { return req("post","/api/postUser",params) };
// -
export const userPwd = (params) => { return req("post", "/api/restPassword", params) };
// -
export const userState = (params) => { return req("post","/api/updateUserState", params) };


/**
 * 
 */
//-
export const getStationList = (params) =>{ return axiosGet('/api/getStationList',params) };
// 
export const postStation = (params) =>{ return req('post','/api/postStation',params) };


/**
 *  
 **/
// -
export const roleList = (params) => { return axiosGet("/api/getRoleList", params) };
// 
export const postRole = (params) => { return req('post','/api/postRole',params) };


/**
 * 
 */
//list
export const getDeptList = (params) => { return axiosGet("/api/getDeptList", params) };
//list,
export const getDeptListWithPage = (params) => { return axiosGet("/api/getDeptListWithPage", params) };
//
export const postDept = (params) => { return req('post','/api/postDept',params)};


