/**
 *   
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="">
        <el-input size="small" v-model="formInline.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input size="small" v-model="formInline.code" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()"></el-button>
      </el-form-item>
    </el-form>
    <!---->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;">
      <el-table-column align="center" type="index" width="60">
      </el-table-column>
      <el-table-column sortable prop="name" label="" width="150">
      </el-table-column>
      <el-table-column sortable prop="type" label="" width="100">
      </el-table-column>
      <el-table-column sortable prop="code" label="" width="100">
      </el-table-column>
      <el-table-column sortable prop="parentsName" label="" width="150">
      </el-table-column>
      <el-table-column sortable prop="leaderName" label="" width="120">
      </el-table-column>
      <el-table-column sortable prop="description" label="" width="200">
      </el-table-column>
      <el-table-column sortable prop="updatedTime" label="" width="160">
        <template slot-scope="scope">
          <div>{{scope.row.updatedTime|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column align="center" label="" min-width="180">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="deleteDept(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
    <!--  -->
    <el-dialog :title="title" :visible.sync="editFormVisible" width="30%" @click="closeDialog">
      <el-form label-width="120px" :model="editForm" :rules="rules" ref="editForm">
        <el-form-item label="" prop="name">
          <el-input size="small" v-model="editForm.name" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="code">
          <el-input size="small" v-model="editForm.code" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select size="small" clearable v-model="editForm.type" placeholder="">
            <el-option label="1" value="1"></el-option>
            <el-option label="2" value="2"></el-option>
            <el-option label="3" value="3"></el-option>
            <el-option label="4" value="4"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=""  prop="parentsId">
          <el-select size="small" clearable v-model="editForm.parentsId" placeholder="">
            <el-option v-for="item in deptList" :key="item.id" :label="item.name"  :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=""  prop="leaderId">
          <el-select size="small" clearable v-model="editForm.leaderId" placeholder="">
            <el-option v-for="item in userList" :key="item.uid" :label="item.realName"  :value="item.uid"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input type="textarea" size="small" v-model="editForm.description" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="state">
          <el-switch  v-model="editForm.state" active-value='1' inactive-value='0'  active-color="#13ce66" inactive-color="#ff4949"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeDialog"></el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitForm('editForm')"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { userList, getDeptList, getDeptListWithPage, postDept } from '../../api/userMG'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      editFormVisible: false, //
      title: '',
      editForm: {
        id: '',
        name: '',
        code: '',
        type: '',
        parentsId: '',
        leaderId: '',
        description: '',
        state: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      // rules
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        code: [{ required: true, message: '', trigger: 'blur' }],
        type: [{ required: true, message: '', trigger: 'blur' }]
      },
      formInline: {
        pageNum: 1,
        pageSize: 10,
        name: '',
        code: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      // 
      deleteDeptParamer: {
        id: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      tokenparm: {
        usertoken: sessionStorage.getItem('logintoken')
      }, 
       //
      userListParams: {
          pageNum : 1,
          pageSize : 1000,
          usertoken: sessionStorage.getItem('logintoken')
      },
      listData: [], //
      userList: [], //
      deptList: [], //
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */

  /**
   * 
   */
  created() {
    this.getdata(this.formInline)
    this.getDeptDownList(this.tokenparm)
    this.getUserDownList(this.userListParams)
  },

  /**
   * 
   */
  methods: {
    // 
    getdata(parameter) {
      getDeptListWithPage(parameter)
        .then(res => {
          this.loading = false
          if (res.code != 200) {
            this.$message({
              type: 'info',
              message: res.data
            })
          } else {
            this.listData = res.data.list
            console.log(this.listData)
            // 
            this.pageparm.currentPage = this.formInline.pageNum
            this.pageparm.pageSize = this.formInline.pageSize
            this.pageparm.total = res.data.total
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    callFather(parm) {
      this.formInline.pageNum = parm.currentPage
      this.formInline.pageSize = parm.pageSize
      this.getdata(this.formInline)
    },
    // 
    search() {
      console.log(this.formInline)
      this.getdata(this.formInline)
    },
    //
    handleEdit: function(index, row) {
      this.editFormVisible = true
      var i = JSON.stringify(row);
        console.log(i)
      if (row != undefined && row != 'undefined') {
        console.log("row.state"+row.state)
        this.title = ''
        this.editForm.id = row.id
        this.editForm.name = row.name
        this.editForm.code = row.code
        this.editForm.type = row.type
        this.editForm.leaderId = row.leaderId
        this.editForm.state = row.state
        this.editForm.parentsId = row.parentsId
      } else {
        this.title = ''
        this.editForm.id = ''
        this.editForm.name = ''
        this.editForm.code = ''
        this.editForm.type = ''
        this.editForm.leaderId = ''
        this.editForm.state = ''
      }
    },
    // 
    submitForm(editData) {
      this.$refs[editData].validate(valid => {
        if (valid) {
          console.log(this.editForm)
          postDept(this.editForm)
            .then(res => {
              this.editFormVisible = false
              this.loading = false
              if (res.code == 200) {
                this.getdata(this.formInline)
                this.$message({
                  type: 'success',
                  message: ''
                })
              } else {
                this.$message({
                  type: 'info',
                  message: res.data
                })
              }
            })
            .catch(err => {
              this.editFormVisible = false
              this.loading = false
              this.$message.error('')
            })
        } else {
          return false
        }
      })
    },
    getDeptDownList(param){
      getDeptList(param).then(res =>{
        if(res.code == 200){
          this.deptList = res.data
        }else{
          this.$message.error(res.data)
        }
      }).catch(err => {
        this.$message.error('')
      })
    },
    getUserDownList(param){
      userList(param).then(res =>{
        if(res.code == 200){
          this.userList = res.data.list
        }else{
          this.$message.error(res.data)
        }
      }).catch(err=>{
        this.$message.error('')
      })
    },
    // 
    deleteDept(index, row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
          this.deleteDeptParamer.id = row.id
          postDept(this.deleteDeptParamer)
            .then(res => {
              if (res.code == 200) {
                this.$message({
                  type: 'success',
                  message: '!'
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.data
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('')
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    // 
    closeDialog() {
      this.editFormVisible = false
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
</style>

 


