/**
 * 
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
      </el-form-item>
    </el-form>
    <!---->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;">
      <el-table-column align="center" type="index" width="60">
      </el-table-column>
      <el-table-column sortable prop="title" label="" width="180">
      </el-table-column>
      <el-table-column sortable prop="applicationReason" label="" width="180">
      </el-table-column>
      <el-table-column sortable prop="createdName" label="" width="180">
      </el-table-column>
      <el-table-column sortable prop="level" label="" width="100">
      </el-table-column>
      <el-table-column sortable prop="createdTime" label="" width="200">
        <template slot-scope="scope">
          <div>{{scope.row.createdTime|timestampToTime}}</div>
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column align="center" label="" min-width="300">
        <template slot-scope="scope">
          <el-button size="mini" type="primary"  @click="handleEdit(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
    <!--  -->
    <el-dialog :title="title" :visible.sync="editFormVisible" width="30%" @click="closeDialog">
      <el-form label-width="120px" :model="editForm" :rules="rules" ref="editForm">
        <el-form-item label="" prop="title">
          <el-input size="small" disabled v-model="editForm.title" auto-complete="off" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="createdName">
          <el-input size="small" disabled v-model="editForm.createdName" auto-complete="off" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="isPass">
          <el-switch disabled v-model="editForm.isPass" nshow active-value="1" inactive-value="0"  active-color="#13ce66" inactive-color="#ff4949"></el-switch>
        </el-form-item>
        <el-form-item label="" prop="unPassReason">
          <el-input size="small" disabled type="textarea" v-model="editForm.unPassReason" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import { getUserProcessInfo } from '../../api/processMG'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      editFormVisible: false, //
      title: '',
      editForm: {
        title:'',
        id:'',
        createdName:'',
        isPass: '',
        unPassReason: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      // rules
      rules: {
        isPass: [
          { required: true, message: '', trigger: 'blur' }
        ],
        payOpen: [{ required: true, message: '', trigger: 'blur' }]
      },
      formInline: {
        pageNum: 1,
        pageSize: 10,
        uid:'',
        usertoken: sessionStorage.getItem('logintoken')
      },
      //
      typeList: [],
      // 
      seletedata: {
        id: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      user:{},
      usertokenParamer :{
        uid:'',
        pageNum:'',
        pageSize:'',
        usertoken: sessionStorage.getItem('logintoken')
      },
      userparm: [], //
      listData: [], //
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */

  /**
   * 
   */
  created() {
    this.user = JSON.parse(sessionStorage.getItem('userdata'))
    this.formInline.uid = this.user.uid
    this.getdata(this.formInline)
  },

  /**
   * 
   */
  methods: {
    // 
    getdata(parameter) {
      this.loading = true
      /***
       *  
       */
      getUserProcessInfo(parameter)
        .then(res => {
          this.loading = false
          if (res.code == 200) {
            this.listData = res.data.list
            // 
            this.pageparm.currentPage = this.formInline.pageNum
            this.pageparm.pageSize = this.formInline.pageSize
            this.pageparm.total = res.data.total
          } else {
            this.$message({
              type: 'info',
              message: res.data
            })
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    callFather(parm) {
      this.formInline.page = parm.currentPage
      this.formInline.limit = parm.pageSize
      this.getdata(this.formInline)
    },
    // 
    search() {
      this.getdata(this.formInline)
    },
    //
    handleEdit: function(index, row) {
      this.editFormVisible = true
      this.editForm.title = row.title
      this.editForm.createdName = row.createdName
      this.editForm.id = row.nodeId
      this.editForm.isPass = row.isPass
      this.editForm.unPassReason = row.unPassReason
    },
    // 
    submitForm(editData) {
      this.$refs[editData].validate(valid => {
        if (valid) {
          postProcess(this.editForm)
            .then(res => {
              this.editFormVisible = false
              this.loading = false
              if (res.code == 200) {
                this.getdata(this.formInline)
                this.$message({
                  type: 'success',
                  message: ''
                })
              } else {
                this.$message.error(res.data)
              }
            })
            .catch(err => {
              this.editFormVisible = false
              this.loading = false
              this.$message.error('')
            })
        } else {
          return false
        }
      })
    },
    // 
    closeDialog() {
      this.editFormVisible = false
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
</style>

 
 


