/**
 *   
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-form :inline="true" :model="formInline" class="user-search">

      <el-form-item label="">
        <el-input clearable size="small" v-model="formInline.rname" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleEdit()"></el-button>
      </el-form-item>
    </el-form>
    <!---->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;">
      <el-table-column align="center" type="index" width="60">
      </el-table-column>
      <el-table-column sortable prop="rname" label="" width="150">
      </el-table-column>
      <el-table-column sortable prop="rdescription" label="" width="200">
      </el-table-column>
      <el-table-column sortable prop="createdTime" label="" width="200">
        <template slot-scope="scope">
          <div>{{scope.row.createdTime|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column sortable prop="updatedTime" label="" width="200">
        <template slot-scope="scope">
          <div>{{scope.row.updatedTime|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column align="center" label="" min-width="200">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="deleteROle(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
    <!--  -->
    <el-dialog :title="title" :visible.sync="editFormVisible" width="30%" @click='closeDialog("edit")'>
      <el-form label-width="120px" :model="editForm" ref="editForm" :rules="rules">
        <el-form-item label="" prop="rname">
          <el-input size="small" v-model="editForm.rname" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="rdescription">
          <el-input size="small" type="textarea" v-model="editForm.rdescription" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click='closeDialog("edit")'></el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitForm('editForm')"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { roleList, postRole } from '../../api/userMG'
import Pagination from '../../components/Pagination'
export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      editFormVisible: false, //
      menuAccessshow: false, //
      title: '',
      editForm: {
        rid: '',
        rname: '',
        rdescription: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      // rules 
      rules: {
        rdescription: [
          { required: true, message: '', trigger: 'blur' }
        ],
        rname: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      formInline: {
        pageNum: 1,
        pageSize: 10,
        rname:'',
        usertoken: sessionStorage.getItem('logintoken')
      },
      // 
      seletedata: {
        rid: '',
        usertoken: sessionStorage.getItem('logintoken')
      },
      listData: [], //
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 
  components: {
    Pagination
  },
  /**
   * 
   */

  watch: {},

  /**
   * 
   */

  created() {
    this.getdata(this.formInline)
  },

  /**
   * 
   */

  methods: {
    // 
    getdata(parameter) {
      /***
       * 
       */
      roleList(parameter)
        .then(res => {
          this.loading = false
          if (res.code != 200) {
            this.$message({
              type: 'info',
              message: res.data
            })
          } else {
            this.listData = res.data.list
            // 
            this.pageparm.currentPage = this.formInline.pageNum
            this.pageparm.pageSize = this.formInline.pageSize
            this.pageparm.total = res.data.total
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('')
        })
    },
    // 
    callFather(parm) {
      this.formInline.pageNum = parm.currentPage
      this.formInline.pageSize = parm.pageSize
      this.getdata(this.formInline)
    },
    // 
    search() {
      this.getdata(this.formInline)
    },
    //
    handleEdit: function(index, row) {
      this.editFormVisible = true
      if (row != undefined && row != 'undefined') {
        this.title = ''
        this.editForm.rid = row.rid
        this.editForm.rdescription = row.rdescription
        this.editForm.rname = row.rname
      } else {
        this.title = ''
        this.editForm.rid = ''
        this.editForm.rname = ''
        this.editForm.rdescription = ''
      }
    },
    // 
    submitForm(editData) {
      this.$refs[editData].validate(valid => {
        if (valid) {
          postRole(this.editForm)
            .then(res => {
              this.editFormVisible = false
              this.loading = false
              if (res.code == 200) {
                this.getdata(this.formInline)
                this.$message({
                  type: 'success',
                  message: ''
                })
              } else {
                this.$message({
                  type: 'info',
                  message: res.data
                })
              }
            })
            .catch(err => {
              this.editFormVisible = false
              this.loading = false
              this.$message.error('')
            })
        } else {
          return false
        }
      })
    },
    // 
    deleteROle(index, row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        console.log("")
          this.seletedata.rid = row.rid
          postRole(this.seletedata)
            .then(res => {
              if (res.code == 200) {
                this.$message({
                  type: 'success',
                  message: ''
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.data
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('')
            })
        }).catch(() => {
          console.log("")
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    // 
    closeDialog(dialog) {
      if (dialog == 'edit') {
        this.editFormVisible = false
      } else if (dialog == 'perm') {
        this.menuAccessshow = false
      }
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
</style>

 


